import React, { useEffect, useState } from 'react'
import { Link } from "react-router-dom";

export default function Header({ Props }) {
  const navItems = [
    {
      label: ' ',
      to: '/intro1',
      subItems: [
        { label: '  ', to: '/intro1' },
        { label: ' ', to: '/intro2' },
        { label: '', to: '/intro3' },
        { label: '', to: '/intro4' },
      ],
    },
    {
      label: ' ',
      to: '/service1',
      subItems: [
        { label: '', to: '/service1' },
        { label: '', to: '/service2' },
        { label: '', to: '/service3' },
        { label: '', to: '/service4' },
        { label: '', to: '/service5' },
      ],
    },
    {
      label: ' ',
      to: '/send',
      subItems: [{ label: ' ', to: '/send' }],
    },
  ];

  const [scrollPosition, setScrollPosition] = useState(0);
  const [moBtn, setMoBtn] = useState(false);
  const [activeNav, setActiveNav] = useState(null);

  //  
  const updateScroll = () => {
    setScrollPosition(window.scrollY || document.documentElement.scrollTop);
  };
  useEffect(() => {
    window.addEventListener('scroll', updateScroll);
  });

  //  
  const toggleNav = () => {
    setMoBtn(!moBtn);
  }

  // 2  
  const toggleTowNav = (index) => {
    setActiveNav(index === activeNav ? null:index);
  } 

  return (
    <header
      id="header"
      className={`${scrollPosition < 10 ? '' : 'scroll'} ${Props}`}
    >
      <div className="position_wrap">
        <div className="w_set">
          <div className="hd_wrap">
            <h1 className="logo">
              <Link to="/">
                <img
                  src={require('../assets/images/img/logo.png')}
                  alt="() "
                />
              </Link>
            </h1>
            <nav>
              <button
                type="button"
                className={`mo_nav ${moBtn ? 'on' : ''}`}
                onClick={toggleNav}
              >
                <span></span>
                <span></span>
                <span></span>
              </button>
              <ul className={`nav_wrap ${moBtn ? 'on' : ''}`}>
                {navItems.map((item, index) => {
                  return (
                    <li key={index}>
                      <Link to={item.to} 
                      onClick={() => {
                        setMoBtn(false);
                      }}
                      >{item.label}</Link>
                      <button
                        type="button"
                        onClick={() => toggleTowNav(index)}
                        className={activeNav === index ? 'on' : ''}
                      >
                         
                      </button>
                      <ul className={activeNav === index ? 'on' : ''}>
                        {item.subItems.map((subItems, subIndex) => {
                          return (
                            <li key={subIndex}>
                              <Link 
                              to={subItems.to} 
                              onClick={() => 
                                {setMoBtn(false); setActiveNav(null);
                              }}
                              >{subItems.label}</Link>
                            </li>
                          );
                        })}
                      </ul>
                    </li>
                  );
                })}
              </ul>
              <a href="tel:024612203" className="btn color_1">
                 
              </a>
            </nav>
          </div>
        </div>
      </div>
    </header>
  );
}
