import axios from 'axios';
import React, { useCallback, useState } from 'react';

export default function Send() {
  const checkboxList = ['', '', '', '', ''];

  const [email, setEmail] = useState('');
  const [title, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [number, setNumber] = useState('');
  const [checkedList, setCheckedList] = useState([]);

  const emailHandler = (e) => {
    e.preventDefault();
    setEmail(e.target.value);
  };
  const subjectHandler = (e) => {
    e.preventDefault();
    setSubject(e.target.value);
  };
  const messageHandler = (e) => {
    e.preventDefault();
    setMessage(e.target.value);
  };
  const numberHandler = (e) => {
    e.preventDefault();
    setNumber(e.target.value);
  };
  const onCheckedItem = useCallback(
    (checked, id) => {
      const value = checkboxList[parseInt(id.substring(2)) - 1];
      if (checked) {
        setCheckedList(prev => [...prev, value]);
      } else {
        setCheckedList(checkedList.filter(el => el !== value));
      }
    },
    [checkedList]
  );

  const submitHandler = useCallback(
    (e) => {
      e.preventDefault();
      if (number && email && title && message && checkedList) {
        console.log(number, email, title, message, checkedList);
        const payload = {
          number: number,
          email: email,
          title: title,
          message: message,
          checkedList: checkedList
        };
        axios
          .post('http://localhost:8001/server/mail', payload)
          .then(res => console.log(res))
          .catch(err => console.error(err));
      }
    },
    [number, email, title, message]
  );
  
  return (
    <div className="w_set">
      <div className="send_inner">
        <div className="send_wrap">
          <div className="txt_wrap">
            <p>
              <span>  </span>
              <span>  </span>
            </p>
            <p>  .</p>
          </div>
          <div className="form_wrap">
            <p className="essen_info">
              <span className="essen type_l"> </span>
            </p>
            <form onSubmit={submitHandler}>
              <ul className="form_list">
                <li>
                  <p></p>
                  <div className="input_wrap">
                    <input
                      type="text"
                      id="send_name"
                      placeholder=" "
                      required
                      onChange={subjectHandler}
                    />
                    <label htmlFor="send_name"></label>
                  </div>
                </li>
                <li>
                  <p></p>
                  <div className="input_wrap">
                    <input
                      type="number"
                      id="send_num"
                      placeholder="'-'  "
                      required
                      onChange={numberHandler}
                    />
                    <label htmlFor="send_num"></label>
                  </div>
                </li>
                <li>
                  <p></p>
                  <div className="input_wrap">
                    <input
                      type="text"
                      id="send_email"
                      placeholder=" "
                      required
                      onChange={emailHandler}
                    />
                    <label htmlFor="send_email"></label>
                  </div>
                </li>
                <li>
                  <p> </p>
                  <div className="input_wrap">
                    <ul className="ck_wrap">
                      {
                        checkboxList.map((item, idx) => {
                          return (
                            <li className="checkbox">
                              <input
                                type="checkbox"
                                id={`ck${idx + 1}`}
                                name="ck_list"
                                value={item}
                                onChange={e => {
                                  onCheckedItem(e.target.checked, e.target.id);
                                }}
                              />
                              <label htmlFor={`ck${idx + 1}`}>{item}</label>
                            </li>
                          );
                        })
                      }
                    </ul>
                    <span className="sub_txt"> </span>
                  </div>
                </li>
                <li>
                  <p></p>
                  <div className="input_wrap">
                    <textarea
                      id="send_txt"
                      required
                      onChange={messageHandler}
                    ></textarea>
                    <label htmlFor="send_txt"></label>
                  </div>
                </li>
                {/* <li>
                  <p></p>
                  <div className="input_wrap">
                    <div className="attach">
                      <label htmlFor="send_file">
                        <span></span>
                      </label>
                      <input
                        type="file"
                        id="send_file"
                        onChange={fileHandler}
                      />
                    </div>
                    <ul className="file_list">
                      <li>
                        <span></span>
                        <button className="btn color_2"></button>
                      </li>
                      <li>
                        <span></span>
                        <button className="btn color_2"></button>
                      </li>
                    </ul>
                  </div>
                </li> */}
              </ul>
              <button type="submit" className="btn color_1">
                  
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
